@import url(https://fonts.googleapis.com/css2?family=Roboto&family=Zen+Tokyo+Zoo&display=swap);
.SearchBar{position:relative;color:var(--green);display:flex;flex-direction:column}@media only screen and (min-width: 540px){.SearchBar{flex-direction:row}}.SearchBar__input{height:40px;padding:0 14px;border:var(--green) solid 1px;background:transparent;color:var(--green);font-size:16px;width:250px}.SearchBar__input::-webkit-input-placeholder{opacity:0.5;color:inherit}.SearchBar__input:-ms-input-placeholder{opacity:0.5;color:inherit}.SearchBar__input::placeholder{opacity:0.5;color:inherit}.SearchBar__button{height:40px;padding:0 14px;border:var(--green) solid 1px;background:transparent;color:var(--green);font-size:14px}.SearchBar__button:hover{background:var(--green);color:var(--on-surface);cursor:pointer}.SearchBar__dataList{height:40px;padding:0 4px;border:var(--green) solid 1px;background:var(--surface);font-size:14px;color:var(--green)}@media only screen and (min-width: 540px){.SearchBar__dataList{width:160px}}

.Header{padding:30px 30px 15px 30px;background-color:var(--surface);display:flex;flex-direction:column;justify-content:center;align-items:center}@media only screen and (min-width: 1200px){.Header{padding:60px 30px 30px}}.Header__title{font-size:32px;text-align:center;color:var(--green);font-family:'Rick', cursive;letter-spacing:20px;margin-bottom:30px}@media only screen and (min-width: 480px){.Header__title{font-size:48px;letter-spacing:30px}}@media only screen and (min-width: 720px){.Header__title{font-size:60px;letter-spacing:40px}}@media only screen and (min-width: 1200px){.Header__logo{position:absolute;top:20px;left:20px}}.Header__search-bar{display:flex;justify-content:center}

.PageTitle {
  font-size: 40px;
}

.SectionTitle {
  font-size: 32px;
}

.ArticleTitle {
  font-size: 26px;
}

.SubTitle {
  font-size: 20px;
  opacity: 0.6;
}
.Back{width:100%;height:100%;background:var(--surface);padding:40px 20px;display:flex;flex-direction:column;justify-content:space-between}.Back__item{display:flex}.Back__key{width:30%;font-size:14px;text-transform:uppercase}.Back__key::after{content:':'}.Back__value{width:70%;font-size:16px;text-align:left}

.Front__heading{position:absolute;bottom:0;left:0;right:0;color:white;padding:80px 20px 20px;text-align:right;background-image:linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,0.6) 60%);pointer-events:none}


.Bilateral{position:relative;width:100%;height:100%;border-radius:inherit}.Bilateral:hover .Bilateral__content{-webkit-transform:perspective(1000px) rotateY(-180deg);transform:perspective(1000px) rotateY(-180deg)}.Bilateral:hover .Bilateral__content--back{-webkit-transform:perspective(1000px) rotateY(0deg);transform:perspective(1000px) rotateY(0deg)}.Bilateral__content{width:100%;height:100%;position:absolute;background-color:transparent;border:inherit;border-radius:inherit;overflow:hidden;transition:-webkit-transform 1000ms;transition:transform 1000ms;transition:transform 1000ms, -webkit-transform 1000ms;-webkit-transform:perspective(1000px) rotateY(0deg);transform:perspective(1000px) rotateY(0deg);-webkit-backface-visibility:hidden;backface-visibility:hidden}.Bilateral__content--back{-webkit-transform:perspective(1000px) rotateY(180deg);transform:perspective(1000px) rotateY(180deg)}

.Card{position:relative;width:300px;height:300px;border-radius:20px;margin:10px;overflow:hidden}

.LocationCard{width:100%;height:100%;background:var(--surface);border-radius:inherit;padding:20px;display:flex;flex-direction:column;justify-content:space-between}.LocationCard:hover{color:var(--green)}.LocationCard__title{font-size:30px;margin-top:10px;text-align:center}.LocationCard__line{margin:20px 20px}.LocationCard__list{list-style:none;display:flex;flex-wrap:wrap}.LocationCard__item{display:flex;width:100%;align-items:center;justify-content:space-between;margin-top:10px}.LocationCard__key{font-size:16px;margin-right:10px;text-transform:uppercase}.LocationCard__key::after{content:':'}.LocationCard__value{font-size:20px;text-align:right}

.EpisodeCard{width:100%;height:100%;background:var(--surface);border-radius:inherit;padding:20px;display:flex;flex-direction:column;justify-content:space-between}.EpisodeCard:hover{color:var(--green)}.EpisodeCard__title{font-size:30px;margin-top:10px;text-align:center}.EpisodeCard__line{margin:20px 20px}.EpisodeCard__list{list-style:none;display:flex;flex-wrap:wrap}.EpisodeCard__item{display:flex;width:100%;align-items:center;justify-content:space-between;margin-top:10px}.EpisodeCard__key{font-size:16px;text-transform:uppercase}.EpisodeCard__key::after{content:':'}.EpisodeCard__value{font-size:20px;text-align:right}

.Loading{padding:24px;font-size:24px}

.CardList{list-style:none;display:flex;flex-wrap:wrap;margin:auto;justify-content:center}.CardList__button{padding:14px;border:var(--green) solid 1px;background:transparent;color:var(--green);font-size:14px}.CardList__button:hover{background:var(--green);color:var(--on-surface);cursor:pointer}

.Pagination{list-style:none;display:flex;flex-wrap:wrap;margin-bottom:20px}.Pagination__element{width:40px;height:25px;display:block;border:solid 1px var(--highlighted-surface);text-align:center;line-height:25px;font-size:16px}.Pagination__element:hover{background-color:var(--green)}.Pagination .active{border:solid 2px var(--green);line-height:23px}.Pagination .disabled{cursor:default}.Pagination .disabled:hover{background-color:inherit}

.Char{display:flex;flex-direction:column;width:100%;position:relative}@media only screen and (min-width: 720px){.Char{flex-direction:row}}.Char__content--fluid{flex-grow:1}

@font-face{font-family:'Rick';src:local("Rick"),url(/react-rick-and-morty/static/media/get_schwifty.c3d1dfc6.ttf) format("truetype")}:root{--background: #121212;--surface: #1e1e1e;--primary: #bb86fc;--primary-variant: #3700b3;--secondary: #04dac6;--on-background: #ffffff;--on-surface: #ffffff;--on-primary: #000000;--on-secondary: #000000;--highlighted-surface: #3e3e3e;--red: #ee3333;--green: #11d811;--yellow: yellow}*{box-sizing:border-box;margin:0;padding:0;font-weight:normal}body{background-color:#121212;background-color:var(--background);color:#ffffff;color:var(--on-background);font-family:'Roboto', sans-serif;font-weight:400;font-size:16px;overflow-y:scroll}section{padding:20px 0}hr{margin:10px 0}a{text-decoration:none;color:inherit}button{cursor:pointer}.container{max-width:1300px;margin:0 auto;padding:20px 10px}.link:hover{color:#11d811;color:var(--green)}div.definitionItem{display:flex;align-items:center;margin-bottom:10px;background-color:#1e1e1e;background-color:var(--surface);border-radius:20px;padding:0 20px}div.definitionItem>dt{width:120px;font-size:22px}div.definitionItem>dt::after{content:':'}div.definitionItem>dd{font-family:'Zen Tokyo Zoo', cursive;font-size:44px;text-transform:lowercase;padding-bottom:12px}

